

import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor';
import "mavon-editor/dist/css/index.css";
import axios from 'axios';
export default {
name: 'create',
data() {
return {
title: '',
content: '',
isShowToast:false,
toastText:'',

toolbars:{
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
fullscreen: true, // 
htmlcode: true, // html
help: true, // 
navigation: true, // 
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
preview: true, // 
}
};
},
methods: {

uploadImage:function(pos,file){
var app = this;





var params =new FormData();
params.append('',file);

axios.post('http://sm.ms/api/upload',params,{



headers:{'Content-Type':'multipart/form-data'}
}).then(function(response){


app.$refs.md.$img2Url(pos,response.data.data.url);

});
},

addtopic: function() {

var app = this;

this.$http.post('/topics', {
title: this.title,
content: this.content,
tab: 'dev',
accesstoken:this.$store.state.accesstoken
}).then(function(response) {

if (response.data && response.data.success) {

app.$router.replace('/topiclist/dev');
}
}) .catch(function(error){



app.isShowToast = true;
app.toastText = error.response.data.error_msg;
});
}
},
components: {
Group,
XTextarea,
XButton,
mavonEditor,
Toast
}
};



